.loading {
  position: absolute;
  top: 25%;
  left: 41%;
  color: white;
  text-align: center;
}

.loadingIcon {
  display: inline-block;
  width: 80px;
  height: 80px;

}

.loadingIcon:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: loadingIcon 1.2s linear infinite;
}

@keyframes loadingIcon {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}