.movieList {
    width: 100%;
}

::-webkit-scrollbar {
    width: 10px;
    height: 5px;
}

::-webkit-scrollbar-track {
    background: #212529;
}

::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background: #2d2d2d;
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}