.movie {
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
    white-space: nowrap;
    padding: 15px 0;
}

.movieItem {
    margin-top: 80px;
}

.movieItem:nth-child(1) {
    margin-top: 30px !important;
}

.title {
    margin: 5px 0 5px 10px !important;
    color: white;
}

.posterMovie {
    width: 200px;
    height: 200px;
    margin: 0 5px;
    cursor: pointer;
}

.posterMovie:first-child,
.backdropMovie:first-child {
    margin-left: 30px;
}

.posterMovie:last-child,
.backdropMovie:last-child {
    margin-right: 30px;
}

.backdropMovie {
    width: 250px;
    height: 150px;
    margin: 0 5px;
    cursor: pointer;
}

.posterMovie:hover {
    transition: 0.3s all ease;
    transform: scale(1.1);
}

.backdropMovie:hover {
    transition: 0.3s all ease;
    transform: scale(1.1);
}