.searchForm {
    padding: 100px 0 0 20px;
}

.form {
    background-color: white;
    overflow: hidden;
    width: 40%;
    border-radius: 2px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    position: relative;
}

.enteredMovie {
    width: 100%;
    border: none;
    border-bottom: 1px solid #00bbec;
    color: gray;
    padding: 20px 0 20px 30px;
}

.enteredMovie:focus {
    outline: none;
}

.btnMovie {
    width: 100%;
    border-radius: 50%;
    text-align: right;
    padding: 20px 0;
}

.btnReset,
.btnSearch {
    padding: 10px 20px;
    cursor: pointer;
}

.btnReset {
    border: none;
    color: rgb(57, 57, 57);
    background-color: transparent;
    margin-right: 10px;
}

.btnSearch {
    background-color: #00bbec;
    color: white;
    border: none;
    margin-right: 40px;
}

.btnSearch:hover {
    transition: 0.3s all ease;
    background-color: rgb(58, 58, 58);
}

.btnReset:hover {
    transition: 0.3s all ease;
    background-color: rgb(58, 58, 58);
    color: white;
}

.IconSearch {
    position: absolute;
    right: 0;
}