.movieDetail {
    display: flex;
    margin: 30px 10px;
    justify-content: space-around;
}

.infoMovie {
    width: 50%;
    color: white;
    padding: 20px;
    position: relative;
}

.infoMovie::after {
    content: '';
    position: absolute;
    width: 100px;
    height: 100px;
    top: 0;
    left: 0;
    border-top: 3px solid white;
    border-left: 3px solid white;
}

.infoMovie::before {
    content: '';
    position: absolute;
    width: 100px;
    height: 100px;
    bottom: 0;
    right: 0;
    border-bottom: 3px solid white;
    border-right: 3px solid white;
}

.vote,
.releaseDate {
    margin: 0;
    font-weight: 700;
}

.title {
    padding-bottom: 20px;
    border-bottom: 2px solid white;
}


/* Style: videoMovie + imgMovie */

.imgMovie {
    width: 100%;
    height: 500px;
    border-radius: 5px;
    object-fit: fill;
}

.videoMovie {
    width: 45%;

}