.banner {
    position: relative;
}

.bannerImage {
    width: 100%;
    height: 500px;
    object-fit: fill;
}

.bannerMovie {
    position: absolute;
    top: 25%;
    padding-left: 3%;
    width: 500px;
    color: white;
    height: 300px;
}

.movieTitle {
    margin-bottom: 50px;
}

.movieContent {
    font-size: 14px;
    height: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.button {
    background-color: rgba(125, 124, 124, 0.7);
    border: none;
    border-radius: 2px;
    color: white;
    padding: 10px 30px;
    cursor: pointer;
}

.button:nth-child(3) {
    margin-left: 20px
}

.button:hover {
    box-shadow: 2px 2px 2px rgba(198, 196, 196, 0.7);
    transition: 0.1s all ease;
}