.navbar {
    display: flex;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    background-color: transparent;
    z-index: 2;
    transition: 0.5s all ease;
}

.navbarScroll {
    background-color: rgba(41, 40, 40);
    transition: 0.5s all ease;
}

.navbarTitle {
    color: red;
    margin: 5px 0 10px 20px;
    font-weight: 700;
    cursor: pointer;
}